import fetch from 'isomorphic-fetch';
import Error from 'next/error';
import Story from '../components/Story';
import Layout from '../components/Layout';
import '../styles/global.scss';
import Link from 'next/link';

class Index extends React.Component {

    static async getInitialProps({ req, res, query }) {
        let stories;
        let page;

        try {
            page = Number(query.page) || 1;
            const response = await fetch(`https://node-hnapi.herokuapp.com/news?page=${page}`)
            stories = await response.json();
        } catch ( err ) {
            console.log('err', err)
            stories = [];
        }
        return { stories, page };
    }

    render() {

        const { stories , page} = this.props;

        if ( stories.length === 0  ){ return <Error statusCode={503}/>}

        const content = stories.map((story, index) => {
            return <Story 
                        key={story.id} 
                        story={story}
                    />
        })

        return (
            <Layout title="Hacker News" description="Sample Hacker News App">
                <div>
                    <h1>Hacker News</h1>
                    <div>
                    {content}
                    </div>
                </div>
                <footer>
                    <Link href={`/?page=${page + 1}`}><a>Click Here</a></Link>
                </footer>
            </Layout>
            
        )
    }
}

export default Index;